
@font-face {
  font-family: 'MazeLine';
  
  src: local('zlabyrinths-eye-fs'), url(./fonts/zlabyrinths-eye-fs.ttf) format('truetype');
}

.App {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    font: 1em "MazeLine";
}

.container{
    height: 700px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.information {
    padding-left: 20px;
    align-items: start;
    flex-direction: row;
    align-self: end;
}

